.slider__area {
  display: flex;
  align-items: center;
  width: 100%;
  -webkit-transition: 1s;
  transition: 1s; }

.slider__cell {
  height: 25rem;
  overflow: hidden;
  width: calc(100% / 3);
  padding: 0.5rem;
  flex-shrink: 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  position: relative;
  opacity: 0.3; }
  .slider__cell:hover {
    -webkit-transform: scale(1);
            transform: scale(1); }
  .slider__cell::before {
    content: "";
    display: block;
    position: absolute;
    height: 4px;
    width: 0;
    background: #ff0000;
    left: 0.5rem;
    bottom: 0; }
  .slider__cell--active {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1; }
    .slider__cell--active::before {
      -webkit-transition: 5s linear;
      transition: 5s linear;
      z-index: 1;
      width: calc(100% - 1rem); }
    .slider__cell--active .slider__cell-inner {
      -webkit-transition: 5s linear;
      transition: 5s linear;
      -webkit-transform: translateY(calc(-100% + 35rem));
              transform: translateY(calc(-100% + 35rem)); }

.slider__cell-inner {
  -webkit-transform: translateY(0);
          transform: translateY(0); }

